<template>
  <div class="login">
    <!-- 头部 - NavBar -->
    <van-nav-bar title="会员登录" left-arrow @click-left="$router.go(-1)"/>
    <!-- // 在浏览器记录中前进一步，等同于 history.forward()  => router.go(1)
      // 后退一步记录，等同于 history.back() =>  router.go(-1) -->

      <!-- 主体 -->
      <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>
      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号码" type="text" >
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="4" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" @click="getPicCode" alt="">
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">{{second===totalSecond?'获取验证码':second + '秒后重新获取'}} </button>
        </div>
      </div>
      <div class="login-btn" @click="login">登录</div>
    </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, codeLogin } from '@/api/login' // 因为导出的不是个实例化对象,而是函数,所以要{ }把它包一下

export default {
  data () {
    return {
      picCode: '', // 用户输入的图形验证码
      picKey: '', // 将来请求传递的图形验证码唯一标识
      picUrl: '', // 存base64,用来渲染的
      totalSecond: 60, // 总秒数
      second: 60, // 当前秒数
      timer: null, // 定时器名字 null和''是一样的,为了辨别timer是个开关而写null
      mobile: '', // 手机号
      msgCode: ''// 短信验证码
    }
  },
  name: 'LoginPage',
  created () {
    // 调用封装的方法函数 有this.的getPicCode()的代表调用的是实例的,没有this.则代表调用的是封装的
    this.getPicCode()
  },
  methods: {
    // 获取图形验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode() // 解构赋值的用法,看不懂可以看下面被注释的两行
      // this.picUrl = res.data.base64 // 有个base64的数据,是一个验证码图片数据url,可以用于渲染回页面
      // this.picKey = res.data.key
      this.picUrl = base64
      this.picKey = key // key唯一标识
    },
    // 校验手机号 和 图形验证码 是否合法
    // 通过校验则返回true,否则返回false
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) { // \d 是只匹配数字
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) { // \w 是匹配数字和字母下划线的多个字符
        this.$toast('请输入正确的验证码')
        return false
      }
      return true
    },
    // 获取短信验证码
    async getCode () {
      if (!this.validFn()) return // 没通过校验就不用往下走了

      // 当前没有计时器开着 且 两个秒数归位 才能开启倒计时
      if (!this.timer && this.second === this.totalSecond) {
        // 发送请求(如果响应的state非200,最好抛出一个Promise错误,await只会等待成功的Promise,就不会往下走了)
        await getMsgCode(this.picCode, this.picKey, this.mobile)
        this.$toast('短信发送成功,注意查收')
        // 因为这个接口没有判断验证码是否正确的功能,所以我们装作它有这个功能即可

        // 开启倒计时
        // !本质:将$toast方法注册挂载到了Vue原型上 Vue.prototype.$toast = xxx
        //! 所以我们在这个Vue项目的组件中能任意调用
        this.timer = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.totalSecond
          }
        }, 1000)
      }
    },
    // 登录
    async login () {
      if (!this.validFn()) {
        return
      }
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('请输入正确的手机验证码')
        return
      }
      const res = await codeLogin(this.mobile, this.msgCode)
      // console.log(res)
      // 将个人权证传进vuex模块的user里面
      this.$store.commit('user/setUserInfo', res.data)
      this.$notify({
        type: 'success',
        message: '登陆成功',
        duration: 1000,
        color: '#548B54',
        background: '#C1FFC1'
      })
      setTimeout(() => {
        // 进行判断,看地址栏有无回跳地址
        // 如果有 => 说明是其他网页拦截到登录页面来的,需要回跳回去
        // 如果没有 => 正常去首页即可
        /* 已被优化变成两行
        const backUrl = this.$route.query.backUrl
        if (backUrl) {
          this.$router.push(`${backUrl}`)
          return
        }
        this.$router.push('/') // 登录成功了,跳转到首页 */
        const url = this.$route.query.backUrl || '/'
        this.$router.replace(url)
      }, 1000)

      // $ 开发的时候只考虑成功的响应,到后面统一将失败放进响应拦截器中处理
    }
  },
  // 离开页面时清除定时器
  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
