// 此处用于存放所有登录相关的接口请求
import request from '@/utils/request'
// ———— @/utils/request 指向一个包含了 Axios 实例的文件
// 而导出包含了 Axios 实例的导出名叫request,所以把request当成axios来用即可

// 1.获取图形验证码的
export const getPicCode = () => { // getPicCode是这个函数的函数名,而()代表暂时没有形参
  return request.get('/captcha/image')
  // | 必须要加return,不然这个getPicCode()就是空的(仅用接口不接收),要把接口返回的值return给getPicCode()
}
/* 复习箭头函数
如果一个箭头函数没有参数，你仍然需要使用括号来表示参数列表为空，不能省略箭头函数的参数部分。
所以，正确的写法是：
export const login = () => {
  // 函数体
};
*/

// 2.获取短信验证码的
export const getMsgCode = (captchaCode, captchaKey, mobile) => {
  return request.post('/captcha/sendSmsCaptcha', {
    form: {
      captchaCode,
      captchaKey,
      mobile
    }
  })
}

// 3.登录接口
export const codeLogin = (mobile, smsCode) => {
  return request.post('/passport/login', {
    form: {
      isParty: false,
      mobile,
      partyData: {},
      smsCode
    }
  })
}
